import React, {useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {Link} from "react-router-dom";
import {selectCartItem} from "../redux/cart/selectors";
import {addItem} from "../redux/cart/slice";

type PizzaBlockProps = {
    pizza: {
        category: string;
        id: number;
        imageUrl: string;
        name: string;
        price: number;
        rating: number;
        types: number[];
        sizes: number[];
    }
}

const PizzaBlock: React.FC<PizzaBlockProps> = ({pizza}) => {
    const [activeType, setActiveType] = useState(0);
    const [activeSize, setActiveSize] = useState(0);

    const dispatch = useDispatch();
    const cartItem = useSelector(selectCartItem(pizza.id));
    const count = cartItem ? cartItem.count : 0;

    const typesArray = ['тонкое', 'традиционное'];
    const sizesArray = [26, 30, 40];


    const addPizzaToCart = () => {
        const item = {
            count: 0,
            id: pizza.id,
            imageUrl: pizza.imageUrl,
            name: pizza.name,
            price: pizza.price,
            type: typesArray[activeType],
            size: sizesArray[activeSize],
        };

        dispatch(addItem(item));
    }

    return (
        <div className="pizza-block">
            <Link to={`/pizzas/${pizza.id}`}>
                <img
                    className="pizza-block__image"
                    src={pizza.imageUrl}
                    alt={pizza.name}
                />
                <h4 className="pizza-block__title">{pizza.name}</h4>
            </Link>
            <div className="pizza-block__selector">
                <ul>
                    {pizza.types.map((item, index) => <li key={index} onClick={() => setActiveType(index)} className={activeType === index ? 'active' : ''}>{item === 0 ? 'тонкое' : 'традиционное'}</li>)}
                </ul>
                <ul>
                    {pizza.sizes.map((item, index) => <li key={index} onClick={() => setActiveSize(index)} className={activeSize === index ? 'active' : ''}>{item} см.</li>)}
                </ul>
            </div>
            <div className="pizza-block__bottom">
                <div className="pizza-block__price">от {pizza.price} ₴</div>
                <div
                    onClick={addPizzaToCart}
                    className="button button--outline button--add"
                >
                    <svg
                        width="12"
                        height="12"
                        viewBox="0 0 12 12"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                    >
                        <path
                            d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
                            fill="white"
                        />
                    </svg>
                    <span>Добавить</span>
                    {count > 0 && <i>{count}</i>}
                </div>
            </div>
        </div>
    );
};

export default PizzaBlock;
